<template>
  <div class="tabbar">
    <!-- 导航栏 -->
    <div class="top_nav">

      <div class="top_box">

        <!-- logo图 -->
        <div class="logo">
          <van-image
            @click="logo()"
            style="cursor: pointer;"
            round
            width="0.8rem"
            height="0.8rem"
            :src="logo_img"
          />
        </div>

        <!-- 导航栏文字 -->
        <div class="nav">
          <el-menu
            :default-active="activeIndex"
            class="el-menu-demo"
            mode="horizontal"
            @select="handleSelect"
            background-color="#fff"
            text-color="#000"
            active-text-color="#FF7F24"
            style="font-family: '微软雅黑'"
          >
            <el-menu-item index="1"><span style="font-size:20px;font-weight: 700;">首页</span></el-menu-item>
            <el-menu-item index="2"><span style="font-size:20px;font-weight: 700;">产品</span></el-menu-item>
            <el-menu-item index="3"><span style="font-size:20px;font-weight: 700;">行业资讯</span></el-menu-item>
            <el-menu-item index="4"><span style="font-size:20px;font-weight: 700;">品牌故事</span></el-menu-item>
            <el-menu-item index="5"><span style="font-size:20px;font-weight: 700;">加人我们</span></el-menu-item>
            <el-menu-item  @click="ewm1"><span style="font-size:20px;font-weight: 700;">手机端</span></el-menu-item>
            <el-menu-item  @click="ewm2"><span style="font-size:20px;font-weight: 700;">微信客服</span></el-menu-item>
          </el-menu>
        </div>

        
      
      </div>
    </div>
    <van-overlay :show="show" @click="show = false">
      <div @click="show = false" class="wrapper" @click.stop>
            <div class="ewm">
              <div style="font-size: 20px; color: white">手机端网址浏览二维码</div>
              <div>
                <img
                  style="width: 200px; height: 200px"
                  :src="h5_ewm"
                  alt=""
                />
              </div>
            </div>
          </div>
    </van-overlay>
    <van-overlay :show="show2" @click="show2 = false">
      <div @click="show2 = false" class="wrapper" @click.stop>
            <div class="ewm">
              <div style="font-size: 20px; color: white">微信客服二维码</div>
              <div>
                <img
                  style="width: 200px; height: 200px"
                  :src="wx_ewm"
                  alt=""
                />
              </div>
            </div>
          </div>
    </van-overlay>
  </div>
</template>

<script>
export default {
  name: "Tabbar",
  data() {
    return {
      show:false,
      show2:false,
      activeIndex: "",
 
      logo_img:'https://image.baidu.com/search/down?url=https://tvax4.sinaimg.cn/large/006D2oSpgy1h5d8trektlj30hs0hs0tl.jpg',
      h5_ewm:'data:image/png;base64,',
      wx_ewm:'https://image.baidu.com/search/down?url=https://tvax2.sinaimg.cn/large/006D2oSpgy1h5d8zxpkm0j30ap0apwgp.jpg'
    };
  },
  created() {
    this.active();
  },
  methods: {
    logo(){
      this.$router.push('/');
    },
    active() {
      this.activeIndex = this.$route.meta.active;
      //   console.log(this.$route.meta.active);
    },
    handleSelect(key, keyPath) {
      if (key == "1") {
        this.$router.push("/");
      }
      if (key == "2") {
        this.$router.push("/commodity_p");
      }
      if (key == "3") {
        this.$router.push("/information_p");
      }
      if (key == "4") {
        this.$router.push("/story_p");
      }
      if (key == "5") {
        this.$router.push("/addus_p");
      }
     
    },
    ewm1(){
      this.show = !this.show;
    },
    ewm2(){
      this.show2 = !this.show2;
    }
  },
};
</script>

<style lang="scss" scope>
.tabbar {
  min-width: 1900px;
  margin-bottom: 0.1rem;
  margin-top: 0.1rem;
  background-color: white;
  position: sticky;
  position: -webkit-sticky; // 兼容 -webkit 内核的浏览器
  top: 0px; // 必须设一个值，否则不生效
  z-index: 100;
  .top_nav {
    display: flex;
    align-items: center;
    justify-content: center;
    min-width: 1200px;
  }
  .top_nav > ul {
    // display: flex;

  }
  .nav{
    display: flex;
    // justify-content: center;
    align-items: center;
    height:1rem;
  }
  .top_box {
    display: flex;
    justify-content: center;
    // align-items: center;
  }

  .el-menu-item {
    font-size: 18px;
    font-weight: 600;
  }
  .el-menu--horizontal > .el-menu-item.is-active {
    border-bottom: 2px solid #ff7340;
    color: #ff7340;
  }
  .logo{
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: 0.1rem;
  }
  .left_logo {
    display: flex;
    justify-content: center;
    align-items: center;
    // height: 1rem;
  }
  .img{
    margin-left: 20px;
  }
  .left_logo > div {
    display: flex;
    justify-content: center;
    align-items: center;
    min-width: 90px;
    // margin-top: -10px;

  }
  .right_logo{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    min-width: 150px;
    // height: 1rem;
  }
  .phone_num {
    font-size: 16px;
    font-weight: 900;
    width: 150px;
  }
  .fw_time {
    font-size: 14px;
    font-weight: 900;
    width: 280px;
    color: #afafaf;
  }

  .wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
  }
  .ewm {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
  }
}
</style>
